import React, { useEffect, useRef, useState } from 'react';
import './App.css';
import * as d3 from 'd3';
import {ButtonGroup, DropdownButton, Dropdown} from 'react-bootstrap';
function App(props) {
    const reference=useRef();
    const reference2=useRef();
    const reference3=useRef();
    const [etat,setEtat]=useState(props.etat2)
    const [outils, setOutils]=useState()
    const [competences, setCompetences]=useState()
    const [besoins, setBesoins]=useState() 
    useEffect(()=>{
     props.outils.forEach(element => {
        if(element[1].includes("je suis expert(e)")){
            element[1]=4
        } else if(element[1].includes("je connais un peu")){
            element[1]=2     
        } else if(element[1].includes("je connais bien")){
            element[1]=3   
        } else{
            element[1]=1
        }
        setOutils(props.outils)
     });
     props.competences.forEach(element => {
        if(element[1].includes("je suis expert(e)")){
            element[1]=4
        } else if(element[1].includes("je connais un peu")){
            element[1]=2     
        } else if(element[1].includes("je connais bien")){
            element[1]=3   
        } else{
            element[1]=1
        }
        setCompetences(props.competences)
     });  
     props.besoins.forEach(element => {
        if(element[1].includes("Besoin urgent")){
            element[1]=4
        } else if(element[1].includes("Besoin d'approfondissement")){
            element[1]=3   
        } else if(element[1].includes("Pas besoin")){
            element[1]=1   
        } else{
            element[1]=2
        }

        setBesoins(props.besoins)
     }); 
     const width=window.innerWidth/2;
     const height=600;
     let color= d3.scaleLinear()
                     .domain([0,4]) 
                     .range(["red","green"])
    let opac= d3.scaleLinear()
                     .domain([0,4]) 
                     .range([0,8])                 
     const canvas=d3.select(reference.current)
     .append("svg")
     .attr("width",width)
     .attr("height",height)
     .style("background-color", "rgba(0,0,0,0.9)")
     .style("padding",0)
     .style("margin-left",window.innerWidth/4)

     canvas.selectAll("rect")
     .data(props.outils)
     .enter()
     .append("rect")
     .attr("class","rectangles")
     .attr("x",(d,i)=>(((i)*width/props.outils.length))+10)
     .attr("y",(d,i)=>height-d[1]*100-15)
     .attr("width",function(d){return (width/props.outils.length)})
     .attr("height",(d,i)=>d[1]*100)
     .attr("fill",function(d){return color(d[1])})
     .style("opacity",function(d){return 1})

     canvas.append("rect")
     .attr("x",10)
     .attr("y",10)
     .attr("width",45)
     .attr("height",10)
     .attr("fill",function(d){return color(1)})
     canvas.append("rect")
     .attr("x",10)
     .attr("y",40)
     .attr("width",45)
     .attr("height",10)
     .attr("fill",function(d){return color(2)})
     canvas.append("rect")
     .attr("x",10)
     .attr("y",70)
     .attr("width",45)
     .attr("height",10)
     .attr("fill",function(d){return color(3)})
     canvas.append("rect")
     .attr("x",10)
     .attr("y",100)
     .attr("width",45)
     .attr("height",10)
     .attr("fill",function(d){return color(4)})

     canvas.append("text")
     .attr("x",60)
     .attr("y",20)
     .text("je connais pas")
     .style("fill","white")
     canvas.append("text")
     .attr("x",60)
     .attr("y",50)
     .text("je connais un peu")
     .style("fill","white")
     canvas.append("text")
     .attr("x",60)
     .attr("y",80)
     .text("je connais bien")
     .style("fill","white")
     canvas.append("text")
     .attr("x",60)
     .attr("y",110)
     .text("je suis expert(e)")
     .style("fill","white")

     const canvas2=d3.select(reference2.current)
     .append("svg")
     .attr("width",width)
     .attr("height",height)
     .style("background-color", "rgba(0,0,0,0.9)")
     .style("padding",0)
     .style("margin-left",window.innerWidth/4)

     canvas2.selectAll("rect")
     .data(props.competences)
     .enter()
     .append("rect")
     .attr("class","rectangles")
     .attr("x",(d,i)=>(((i)*width/props.competences.length))+10)
     .attr("y",(d,i)=>height-d[1]*100-15)
     .attr("width",function(d){return (width/props.competences.length)-10})
     .attr("height",(d,i)=>d[1]*100)
     .attr("fill",function(d){return color(d[1])})
     .style("opacity",function(d){return 1})

     canvas2.append("rect")
     .attr("x",10)
     .attr("y",10)
     .attr("width",45)
     .attr("height",10)
     .attr("fill",function(d){return color(1)})
     canvas2.append("rect")
     .attr("x",10)
     .attr("y",40)
     .attr("width",45)
     .attr("height",10)
     .attr("fill",function(d){return color(2)})
     canvas2.append("rect")
     .attr("x",10)
     .attr("y",70)
     .attr("width",45)
     .attr("height",10)
     .attr("fill",function(d){return color(3)})
     canvas2.append("rect")
     .attr("x",10)
     .attr("y",100)
     .attr("width",45)
     .attr("height",10)
     .attr("fill",function(d){return color(4)})

     canvas2.append("text")
     .attr("x",60)
     .attr("y",20)
     .text("je connais pas")
     .style("fill","white")
     canvas2.append("text")
     .attr("x",60)
     .attr("y",50)
     .text("je connais un peu")
     .style("fill","white")
     canvas2.append("text")
     .attr("x",60)
     .attr("y",80)
     .text("je connais bien")
     .style("fill","white")
     canvas2.append("text")
     .attr("x",60)
     .attr("y",110)
     .text("je suis expert(e)")
     .style("fill","white")

     const canvas3=d3.select(reference3.current)
     .append("svg")
     .attr("width",width)
     .attr("height",height)
     .style("background-color", "rgba(0,0,0,0.9)")
     .style("padding",0)
     .style("margin-left",window.innerWidth/4)

     canvas3.selectAll("rect")
     .data(props.besoins)
     .enter()
     .append("rect")
     .attr("class","rectangles")
     .attr("x",(d,i)=>(((i)*width/props.besoins.length))+10)
     .attr("y",(d,i)=>height-d[1]*100-15)
     .attr("width",function(d){return (width/props.besoins.length)-10})
     .attr("height",(d,i)=>d[1]*100)
     .attr("fill",function(d){return color(d[1])})
     .style("opacity",function(d){return 1})

     canvas3.append("rect")
     .attr("x",10)
     .attr("y",40)
     .attr("width",45)
     .attr("height",10)
     .attr("fill",function(d){return color(2)})
     canvas3.append("rect")
     .attr("x",10)
     .attr("y",70)
     .attr("width",45)
     .attr("height",10)
     .attr("fill",function(d){return color(3)})
     canvas3.append("rect")
     .attr("x",10)
     .attr("y",100)
     .attr("width",45)
     .attr("height",10)
     .attr("fill",function(d){return color(4)})

     canvas3.append("text")
     .attr("x",60)
     .attr("y",50)
     .text("Pas besoin")
     .style("fill","white")
     canvas3.append("text")
     .attr("x",60)
     .attr("y",80)
     .text("Besoin d'approfondissement")
     .style("fill","white")
     canvas3.append("text")
     .attr("x",60)
     .attr("y",110)
     .text("Besoin urgent")
     .style("fill","white")
    },[])

  return (
    <React.Fragment>
{etat==1?
<div className="informations" ref={reference} style={{color:"green"}}>
<ButtonGroup style={{marginLeft:"380px"}}>
  <DropdownButton as={ButtonGroup} title="je connais pas" id="bg-vertical-dropdown-1">
    {outils?outils.map((val,key)=>val[1]==1?<Dropdown.Item eventKey="1">{val[0]}</Dropdown.Item>:''):''}
  </DropdownButton>

  <DropdownButton as={ButtonGroup} title="je connais un peu" id="bg-vertical-dropdown-2">
  {outils?outils.map((val,key)=>val[1]==2?<Dropdown.Item eventKey="1">{val[0]}</Dropdown.Item>:''):''}
  </DropdownButton>

  <DropdownButton as={ButtonGroup} title="je connais bien" id="bg-vertical-dropdown-3">
  {outils?outils.map((val,key)=>val[1]==3?<Dropdown.Item eventKey="1">{val[0]}</Dropdown.Item>:''):''}
  </DropdownButton>

  <DropdownButton as={ButtonGroup} title="je suis expert(e)" id="bg-vertical-dropdown-4">
  {outils?outils.map((val,key)=>val[1]==4?<Dropdown.Item eventKey="1">{val[0]}</Dropdown.Item>:''):''}
  </DropdownButton>
</ButtonGroup><br/><br/>
</div>
:etat==2?
<div className="informations" ref={reference2} style={{color:"green"}}>
<ButtonGroup style={{marginLeft:"450px"}}>
  <DropdownButton as={ButtonGroup} title="je connais pas" id="bg-vertical-dropdown-1">
    {competences?competences.map((val,key)=>val[1]==1?<Dropdown.Item eventKey="1">{val[0]}</Dropdown.Item>:''):''}
  </DropdownButton>

  <DropdownButton as={ButtonGroup} title="je connais un peu" id="bg-vertical-dropdown-2">
  {competences?competences.map((val,key)=>val[1]==2?<Dropdown.Item eventKey="1">{val[0]}</Dropdown.Item>:''):''}
  </DropdownButton>

  <DropdownButton as={ButtonGroup} title="je connais bien" id="bg-vertical-dropdown-3">
  {competences?competences.map((val,key)=>val[1]==3?<Dropdown.Item eventKey="1">{val[0]}</Dropdown.Item>:''):''}
  </DropdownButton>

  <DropdownButton as={ButtonGroup} title="je suis expert(e)" id="bg-vertical-dropdown-4">
  {competences?competences.map((val,key)=>val[1]==4?<Dropdown.Item eventKey="1">{val[0]}</Dropdown.Item>:''):''}
  </DropdownButton>
</ButtonGroup><br/><br/>
</div>:etat==3?
<div className="informations" ref={reference3} style={{color:"green"}}>
<ButtonGroup style={{marginLeft:"450px", alignSelf:"center", alignItems:"center"}}>
  <DropdownButton as={ButtonGroup} title="Pas besoin" id="bg-vertical-dropdown-2">
  {besoins?besoins.map((val,key)=>val[1]==2?<Dropdown.Item eventKey="1">{val[0]}</Dropdown.Item>:''):''}
  </DropdownButton>

  <DropdownButton as={ButtonGroup} title="Besoin d'approfondissement" id="bg-vertical-dropdown-3">
  {besoins?besoins.map((val,key)=>val[1]==3?<Dropdown.Item eventKey="1">{val[0]}</Dropdown.Item>:''):''}
  </DropdownButton>

  <DropdownButton as={ButtonGroup} title="Besoin urgent" id="bg-vertical-dropdown-4">
  {besoins?besoins.map((val,key)=>val[1]==4?<Dropdown.Item eventKey="1">{val[0]}</Dropdown.Item>:''):''}
  </DropdownButton>
</ButtonGroup><br/><br/>
</div>:""
}
</React.Fragment>
  );

}

export default App;