import React, { useEffect, useState } from 'react';
import axios from 'axios'
import './App.css';
import * as A from 'csvtojson';
import * as d3 from 'd3';
import {Card, ListGroup, ListGroupItem, CardGroup} from 'react-bootstrap';
import Information from './Information.js'

function App() {
  const [etat,setEtat]=useState(true)
  const [etat2,setEtat2]=useState(0)
  const [dataJson, setDataJson]=useState([])
  const [drive,setDrive]=useState("https://drive.google.com/uc")
  const [data, setData]=useState("")
  const [outils, setOutils]=useState()
  const [competences, setCompetences]=useState()
  const [besoins, setBesoins]=useState() 
  useEffect(()=>{
      let a=[]
     d3.csv("/spreadsheets/d/e/2PACX-1vQHni5Lu--I9WKTWGHOJ3_SBRW47sPTvxxRN6FOaGchhMztr4gm3I5C91EJq1CcGOsmfsOfa2f7PfrF/pub?gid=11903047&single=true&output=csv")
     .then((data)=>{
       console.log(data)
         data.forEach(element => {
            element["Votre photo"]=element["Votre photo"].split("open")[1]
            a.push(element)          
         });
         setDataJson(a) 
    })
  },[]);
  function fonction(e){
      let Competences=[]
      let Outils=[]
      let Besoins=[]
    dataJson.map((val,key)=>{
        if(val["N° étudiant"]==e.target.name){
            Object.keys(val).forEach((e)=>{
                if(e.includes("Quelles outils utilisez vous")){
                    Outils.push([e,val[e]])
                }
                if(e.includes("Quelles sont besoins")){
                    Besoins.push([e,val[e]])
                }
                if(e.includes("Quelles sont vos compétences")){
                    Competences.push([e,val[e]])
                }
            })           
        }
    })
    setOutils(Outils)
    setCompetences(Competences)
    setBesoins(Besoins)
    setEtat(false)
    setEtat2(1)
  }
  function fonction2(e){
    let Competences=[]
    let Outils=[]
    let Besoins=[]
  dataJson.map((val,key)=>{
      if(val["N° étudiant"]==e.target.name){
          Object.keys(val).forEach((e)=>{
              if(e.includes("Quelles outils utilisez vous")){
                  Outils.push([e,val[e]])
              }
              if(e.includes("Quelles sont besoins")){
                  Besoins.push([e,val[e]])
              }
              if(e.includes("Quelles sont vos compétences")){
                  Competences.push([e,val[e]])
              }
          })           
      }
  })
  setOutils(Outils)
  setCompetences(Competences)
  setBesoins(Besoins)
  setEtat(false)
  setEtat2(2)
}
function fonction3(e){
  let Competences=[]
  let Outils=[]
  let Besoins=[]
dataJson.map((val,key)=>{
    if(val["N° étudiant"]==e.target.name){
        Object.keys(val).forEach((e)=>{
            if(e.includes("Quelles outils utilisez vous")){
                Outils.push([e,val[e]])
            }
            if(e.includes("Quelles sont besoins")){
                Besoins.push([e,val[e]])
            }
            if(e.includes("Quelles sont vos compétences")){
                Competences.push([e,val[e]])
            }
        })           
    }
})
setOutils(Outils)
setCompetences(Competences)
setBesoins(Besoins)
setEtat(false)
setEtat2(3)
}
function fonction4(e){
  setEtat(true)
}
  return (
    <div className="containers">
{etat?dataJson.map((val,key)=>(
    console.log(outils,competences,besoins),
<Card style={{ color:'rgba(50,50,50,1)', width: '18rem', marginLeft:'25px', marginTop:'25px', boxShadow: '10px 5px 5px rgba(0,0,0,0.2)' }} border="secondary" key={key}>
  <Card.Img variant="top" src={drive+val["Votre photo"]} style={{height:"300px"}}/>
  <Card.Body>
      <Card.Title>{val["Votre prénom"]} {val["Votre nom"]}</Card.Title>
  </Card.Body>
  <ListGroup className="list-group-flush">
      <ListGroupItem style={{height:'4rem'}}>{val["Votre mail"]}</ListGroupItem>
    <ListGroupItem>{"Num Etu : "+val["N° étudiant"]}</ListGroupItem>
  </ListGroup>
  <Card.Body>
    <button key={key} className='button' onClick={fonction} name={val["N° étudiant"]}>Outils...</button>
    <button key={key} className='button' onClick={fonction2} name={val["N° étudiant"]}>Compétences...</button>
    <button key={key} className='button' onClick={fonction3} name={val["N° étudiant"]}>Besoins...</button>
  </Card.Body>
</Card>
))
:<React.Fragment>
  <button className="retour" onClick={fonction4} style={{backgroundColor:'red',color:"white"}}>Retour...</button>
  <Information outils={outils} competences={competences} besoins={besoins} etat2={etat2}></Information>
  </React.Fragment>}
    </div>
  );
}

export default App;
